.page {
  --primary-border-color: #e1e7ed;
  --primary-text-color: #3d4e61;
  box-sizing: border-box;
  color: var(--primary-text-color);
  font-family: Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.search-bar-form {
  box-sizing: border-box;
  color: var(--primary-text-color);
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar-form__input {
  border-radius: inherit;
  background-image: url("Search.ccd3c07b.svg");
  background-position-y: center;
  background-repeat: no-repeat;
  border: 0;
  flex-grow: 1;
  padding: 0;
}

.search-bar-form__input:focus {
  text-shadow: 1px 3px 3px #00000040;
  background-color: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  outline-offset: 0;
  border-radius: 3px;
  outline: 0;
  font-weight: 400;
  transition: all .19s;
  box-shadow: 0 4px 4px #00000040;
}

.search-bar-form:hover {
  transition: all .19s;
  box-shadow: 0 4px 4px #00000040;
}

.search-bar-form__input::placeholder {
  color: var(--primary-text-color);
  font-family: Avenir, Helvetica, Arial, sans-serif;
}

.search-bar-form--size--l {
  height: 70px;
  margin: 20px 0;
}

.search-bar-form__input--size--l {
  background-position-x: 26px;
  background-size: 19px;
  padding-left: 62px;
}

.search-bar-form__input--size--l::placeholder {
  font-size: 16px;
}

.search-bar-form--size--s {
  height: 42px;
}

.search-bar-form__input--size--s {
  background-position: 12px;
  background-size: 11px;
  padding-left: 33px;
  line-height: 20px;
}

.search-bar-form__input--size--s::placeholder {
  font-size: 14px;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
}

